import React, { useState, useEffect } from "react";
import { Form, Button } from "react-bootstrap";
import { config } from "../configs/endpoints.js";
import serialize from "form-serialize";
import { Link, useHistory, useLocation } from "react-router-dom";
import axios from "axios";

function UserForm() {
  const [validated, setValidated] = useState(false);
  const [user, setUser] = useState(null);
  const history = useHistory();

  const search = useLocation().search;
  const id = new URLSearchParams(search).get("id");
  const view = id ? "Edit" : "Add";

  useEffect(() => {
    axios
      .get(`${config.endpoints.basePath}${config.endpoints.users.list}`)
      .then((response) => {
        const user = response.data.users.filter((u) => u.user_id == id)[0];
        user.status = (user.status === "1")  ?  "true": "";
        setUser(user);
        console.debug(user);
      });
  }, []);

  function getEndpoint() {
    return id
      ? `${config.endpoints.basePath}${config.endpoints.users.edit}`
      : `${config.endpoints.basePath}${config.endpoints.users.add}`;
  }

  function handleSave(event) {
    const form = event.currentTarget;

    if (form.checkValidity() === false) {
      event.preventDefault();
      event.stopPropagation();
    } else {
      // let formData = serialize(form, { hash: true });
      console.log("You clicked submit.");
      event.preventDefault();

      const data = serialize(form, { hash: true });
      const status = data.status ? "1" : "0";
      data.status = status;
      if (id) {
        data.user_id = id;
      }

      axios({
        // Endpoint to send files
        url: getEndpoint(),
        method: "POST",
        headers: {
          // Add any auth token here
          // authorization: "your token comes here",
        },

        // Attaching the form data
        data: data,
      })
        // Handle the response from backend here
        .then((res) => {
          console.debug(res.data.error == "0");
          if (res.data.error == "0") {
            history.push(`${process.env.PUBLIC_URL}/pages/users`);
          } else {
            alert(
              res.data.error
                ? res.data.message
                : "Something Went wrong, Try again later."
            );
          }
        })

        // Catch errors if any
        .catch((err) => {});
    }

    setValidated(true);
    event.preventDefault();
  }

  // handleSubmit2 (event) {
  //   event.preventDefault();
  //   console.debug("data:", this)
  //   // let formData = this.props.auth.getIn(["emailSignIn", this.getEndpoint(), "form"]).toJS();
  //   // this.props.dispatch(emailSignIn(formData, this.getEndpoint()))
  //   //   .then(this.props.next)
  //   //   .catch(() => {});
  // }

  return (
    <div>
      <div className="d-flex  auth px-0">
        <div className="row w-100 mx-0">
          <div className="col-lg-12 mx-auto">
            <div className="page-header">
              <h3 className="page-title"> User</h3>
              <Link
                className="btn btn-primary"
                to={{
                  pathname: `${process.env.PUBLIC_URL}/pages/users`,
                }}
              >
                Back
              </Link>
            </div>

            <div className="col-lg-4 mx-auto">
              <div className="auth-form-light text-left py-5 px-4 px-sm-5">
                <h6 className="text-center font-weight-bold">{view} User</h6>
                <Form
                  className="pt-3"
                  noValidate
                  validated={validated}
                  onSubmit={handleSave}
                >
                  <Form.Group className="mb-3 search-field">
                    <Form.Label>First Name</Form.Label>
                    <Form.Control
                      type="text"
                      defaultValue={user?.first_name}
                      name="first_name"
                      required
                      placeholder="First Name"
                      size="lg"
                      className="h-auto"
                    />
                  </Form.Group>
                  <Form.Group className="mb-3 search-field">
                    <Form.Label>Last Name</Form.Label>
                    <Form.Control
                      type="text"
                      defaultValue={user?.last_name}
                      name="last_name"
                      required
                      placeholder="Last Name"
                      size="lg"
                      className="h-auto"
                    />
                  </Form.Group>
                  <Form.Group className="mb-3 search-field">
                    <Form.Label>Mobile Number</Form.Label>
                    <Form.Control
                      type="tel"
                      defaultValue={user?.mobile_number}
                      name="mobile_number"
                      maxLength="10"
                      required
                      placeholder="Mobile"
                      size="lg"
                      className="h-auto"
                      disabled={(view =='Edit') ? true : false}
                    />
                  </Form.Group>
                  <Form.Group className="mb-3 search-field">
                    <Form.Label>Password</Form.Label>
                    <Form.Control
                      type="password"
                      defaultValue={user?.password}
                      name="password"
                      required
                      placeholder="Password"
                      size="lg"
                      className="h-auto"
                    />
                  </Form.Group>
                  <Form.Group className="mb-3 search-field">
                    <Form.Check
                      className="mx-4"
                      type={"checkbox"}
                      name="status"
                      label={`Status`}
                      defaultChecked={user?.status}
                    />
                  </Form.Group>
                  <div className="mt-3">
                    <Button
                      className="btn btn-block btn-primary btn-lg font-weight-medium auth-form-btn"
                      type="submit"
                    >
                      Save
                    </Button>
                  </div>
                </Form>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
}

export default UserForm;
